import { cilPlus } from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import {
  CButton,
  CFormFloating,
  CFormInput,
  CFormLabel,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
} from "@coreui/react";
import axios from "axios";
import React, { useEffect } from "react";
import { toast } from "react-toastify";
import { api, headers } from "src/views/api";

const FilterSubModal = ({ visible, setVisible, filter, getAllFilters }) => {
  const [value, setValue] = React.useState("");
  const [v, setV] = React.useState([]);
  const [selected, setSelected] = React.useState(null);

  const [children, setChildren] = React.useState({
    title: "",
    values: [],
  });

  const removeDub = (arr) => {
    return arr.filter(
      (v, i, a) =>
        a.findIndex((t) => t._id === v._id && t.name === v.name) === i
    );
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const find = children.values?.find((el) => el?.value === selected);

    if (find) {
      find.values = v;
    } else {
      children.values.push({
        value: selected,
        values: v,
      });
    }

    const filterData = {
      ...filter,
      children,
    };
    delete filterData?.createdAt;
    delete filterData?.updatedAt;
    delete filterData?.__v;
    delete filterData?._id;

    const res = await axios.put(
      `${api}/api/filter/${filter?._id}`,
      filterData,
      { headers: headers }
    );

    if (res?.status === 200) {
      toast.success("با موفقیت تغییرات انجام شد");
      setVisible(false);
      getAllFilters();
    }
  };

  useEffect(async () => {
    const res = await axios.get(`${api}/api/filter/${filter?._id}`, {
      headers: headers,
    });

    if (res?.data?.children?.title?.length) {
      setChildren(res?.data?.children);
    } else {
      setChildren({
        title: "",
        values: [],
      });
    }
  }, [filter]);

  return (
    <CModal visible={visible} onClose={() => setVisible(false)}>
      <CModalHeader onClose={() => setVisible(false)}>
        <CModalTitle>ویرایش فیلتر</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <div className="row">
          <div className="col-md-12">
            <div className="row">
              <div className="col-md-12 mb-3">
                <CFormFloating>
                  <CFormInput
                    type="text"
                    id="name"
                    placeholder="نام فیلتر را وارد کنید"
                    value={children?.title}
                    onChange={(e) =>
                      setChildren({
                        ...children,
                        title: e.target.value,
                      })
                    }
                  />
                  <CFormLabel htmlFor="name">عنوان</CFormLabel>
                </CFormFloating>
              </div>
              <div className="col-md-9 mb-3">
                <select
                  value={selected}
                  class="form-select"
                  aria-label="Default select example"
                  onChange={async (e) => {
                    setSelected(e.target.value);
                    const result = await axios.get(
                      `${api}/api/filter/category/value/${filter?._id}/${e.target.value}`,
                      { headers }
                    );
                    if (result.data?.[0]?.values?.length) {
                      setV(result.data?.[0]?.values);
                    } else {
                      setV([]);
                    }
                  }}
                >
                  <option selected value={null}>
                    انتخاب مقدار
                  </option>
                  {filter?.values?.map((child) => (
                    <option value={child._id}>{child.name}</option>
                  ))}
                </select>
              </div>
              <div className="col-md-12 mb-3">
                <div className="row align-items-center">
                  <div className="col-md-10 mb-3">
                    <CFormFloating>
                      <CFormInput
                        type="text"
                        id="value"
                        value={value}
                        onChange={(e) => setValue(e.target.value)}
                        placeholder="مقادیر فیلتر را وارد و بروی Enter بزنید"
                      />
                      <CFormLabel htmlFor="name">مقادیر</CFormLabel>
                    </CFormFloating>
                  </div>
                  <div className="col-md-2">
                    <CButton
                      shape="rounded-pill"
                      color="primary"
                      onClick={() => {
                        let arr = v;
                        let obj = {
                          name: value,
                        };
                        arr.push(obj);
                        setV(arr);
                        setValue("");
                      }}
                    >
                      <CIcon name={cilPlus} />
                    </CButton>
                  </div>
                  <div className="col-md-12">
                    {v?.map((el) => {
                      return (
                        <CButton
                          color="warning"
                          shape="rounded-pill"
                          onClick={() => {
                            setV(v?.filter((e) => e.name !== el?.name));
                          }}
                        >
                          {el?.name}
                        </CButton>
                      );
                    })}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </CModalBody>
      <CModalFooter>
        <CButton color="secondary" onClick={() => setVisible(false)}>
          انصراف
        </CButton>
        <CButton color="primary" onClick={handleSubmit}>
          ذخیره تغییرات
        </CButton>
      </CModalFooter>
    </CModal>
  );
};

export default FilterSubModal;
